function ProjectsComponent(props){
	const o = props.o
	const [elevation, setElevation] = React.useState(1)
	const onMouseEnter =()=>{setElevation(6)}
	const onMouseLeave =()=>{setElevation(1)}
	return(
		<Grid item md={4} sm={6} xs={12}>
			<Paper style={{paddingLeft:20,paddingRight:20,paddingTop:20,paddingBottom:40, zIndex:999, position:'relative'}} elevation={elevation} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
				<img src={o.img} style={{width:'100%'}}/>
				<Typography align="center" variant="h5">{o.projectName}</Typography>
				<Divider variant="middle" style={{marginTop:20,marginBottom:20}}/>
				<Typography align="center">{o.projectDescription}</Typography>
				<Divider variant="middle" style={{marginTop:20,marginBottom:20}}/>
				<Typography align="center" >Status</Typography>
				<Typography align="center">{o.status}</Typography>										
			</Paper>
		</Grid>
	)
}
